// title image
import title from "./img/popup_title.png";
import buynow from "./img/buy_now.png";

function Popup(){
    return(
        <div className="popup">
            <img src={title} />
            <ul>
                <li><input type="radio" name="quantity"/>x1</li>
                <li><input type="radio" name="quantity"/>x5</li>
                <li><input type="radio" name="quantity"/>x10</li>
            </ul>
            <button>
                <img src={buynow}/>
            </button>
            
        </div>
    )
}

export default Popup;