// counter
import Counter from "./counter";
// images
import title from "../../img/pirate_title.png";
import chest from "../../img/chest.png";

function Pirate(){
    return(
        <div className="inventory_content inventory_pirate">
            <img src={title} />
            <div className="inventory_item">
                    <div className="chest_item">
                        <Counter />
                        <img className="chest" src={chest} />
                    </div>
            </div>
        </div>
    );
}

export default Pirate;