import seaport from "../../img/chest.png"
import  buyNow from "../../img/buy_now.png";
function Seaport(){
    return(
        <div className="side_item inventory_seaport">
            <img src={seaport}/>
            <button>
                <img src={buyNow}/>
            </button>
        </div>
    );
}

export default Seaport;