import React,{Component} from 'react';
import MetaMaskLoginButton from 'react-metamask-login-button';

import logo from "../img/logo.png";

function Header(){
    return(
        <header>
            <div className="container">
                <img className="logo" src={logo}/>
                <MetaMaskLoginButton />
            </div>
        </header>
    );
}


export default Header;