import Pirate from "./inventory/pirate";
import Ship from "./inventory/ship";
import Seaport from "./inventory/seaport";

// title
import Inventory_title from "../img/inventory.png";


function Inventory(){
    return(
        <div className="inventory">
            <img className="title" src={Inventory_title} />
            <Pirate />
            <Ship />
            <Seaport />
        </div>
    );
}

export default Inventory;