import pirate from "../../img/chest.png";
import  buyNow from "../../img/buy_now.png";
function Pirate(){
    return(
        <div className="side_item side_pirate">
            <img src={pirate}></img>
            <button onClick={buyPirate}>
                <img src={buyNow}/>
            </button>
        </div>
    );
}


function buyPirate(){
    alert("I want to buy Pirate!");
}

export default Pirate;