// counter
import Counter from "./counter";

import title from "../../img/ship_title.png";
import chest from "../../img/chest.png";
function Ship(){
    return(
        <div className="inventory_content inventory_ship">
            <img src={title} />
            <div className="inventory_item">
                    <div className="chest_item">
                        <Counter />
                        <img className="chest" src={chest} />
                    </div>
            </div>
        </div>
    );
}

export default Ship;